span.number {
  background: transparent;
  border: 0.125rem solid rgba(255, 255, 255, 1);
  width: 3rem;
  height: 3rem;
  display: inline-block;
  text-align: center;
  top: -2rem;
  margin-left: .5rem;
  position: relative;
  color: rgba(255, 255, 255, 1);
  font-size: 2rem;
  border-radius: 50%;
  line-height: 3rem;
  text-indent: 0rem !important;
  text-align: left;
}

span.number > span {
  position: absolute;
  width: 3rem;
  height: 3rem;
