.target {
  opacity: 0;
  pointer-events: none;
  -ms-touch-action: none;
      touch-action: none;
  position: fixed;
}

.target.visible {
  opacity: 1;
}

/* Johanne */
.target-johanne {
  z-index: 1;
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
}

.target-johanne > video {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  min-width: 50%;
  min-height: 50%;
}

