.link .asset {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  z-index: 0;
  opacity: 0;
  pointer-events: none;
}

.link .asset.visible {
  opacity: 1;
}

.link .asset-inner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

.link .link-inner {
  display: -ms-inline-flexbox;
  display: inline-flex;
  z-index: 999;
}

.link .link-inner a {
  z-index: 1;
  display: inline-block;
}



